@import url(https://fonts.googleapis.com/css?family=Noto+Serif+JP:600|Playfair+Display:900|Heebo:100|Work+Sans:900&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 250vw;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    margin: 0;
    padding: 0;
}

.App-header {
    background: #333333;
    margin: 0;
    padding: 5vh 0 3vh;
    vertical-align: middle;
    text-align: center;
}

.header-title{
    font-family: 'Heebo';
    font-weight: 100;
    font-size: 9vw;
    line-height: 1.5em;
    color: #aaaaaa;
    margin: 0;
    padding: 0;
}

.ScrollMarqueeContainer{
    margin: 10vh 0;
    height: 40vw;
    background: #f3f3ee;
    overflow: hidden;
    white-space: nowrap;
    padding-top: 10vh;
}

.JapaneseText{
    float: left;
    margin: 0 0 5vw 0;
}

.JapaneseText .wrapper{
    margin: 0;
    padding: 0 1vw 0 4vw;
    -webkit-writing-mode: vertical-rl;
        -ms-writing-mode: tb-rl;
            writing-mode: vertical-rl;
    -webkit-text-orientation: upright;
            text-orientation: upright;
    float: right;
    -webkit-column-width: 27em;
       -moz-column-width: 27em;
            column-width: 27em;
    grid-column-gap: 2rem;
    -webkit-column-gap: 2rem;
       -moz-column-gap: 2rem;
            column-gap: 2rem;
    width: 90vw;
}

.JapaneseText .wrapper p{
    font-family: 'Noto Serif JP';
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 0.045em;
    line-height: 1.65em;
    margin: 0 1em;
}


.SlideInBoxes{
    display: block;
    height: 65vw;
    background: #ddddde;
    padding: 0;
    margin: 4vh 0;
    text-align: center;
    vertical-align: middle;
}

@-webkit-keyframes section1-left-box-show{
    from{
        margin-left:  -2vw;
        opacity: 0;
    }

    to{
        margin-left: 12vw;
        opacity: 1;
    }
}

@keyframes section1-left-box-show{
    from{
        margin-left:  -2vw;
        opacity: 0;
    }

    to{
        margin-left: 12vw;
        opacity: 1;
    }
}

@-webkit-keyframes section1-right-box-show{
    from{
        margin-right:  0vw;
        opacity: 0;
    }

    to{
        margin-right: 15vw;
        opacity: 1;
    }
}

@keyframes section1-right-box-show{
    from{
        margin-right:  0vw;
        opacity: 0;
    }

    to{
        margin-right: 15vw;
        opacity: 1;
    }
}

@-webkit-keyframes section1-box-hide{
    from{
        opacity: 1;
    }

    to{
        opacity: 0;
    }
}

@keyframes section1-box-hide{
    from{
        opacity: 1;
    }

    to{
        opacity: 0;
    }
}

.SlideInBoxes .box{
    position: static;
    height: 20vw;
    padding: 1vw 1vw 0.5vw;
    background: #222222;
    clear: both;
}

.SlideInBoxes .box .boxText{
    font-family: 'Playfair Display';
    font-size: 17vw;
    vertical-align: middle;
    line-height: 1em;
    color: #DDDDDD;
}

.SlideInBoxes .box.left{
    float: left;
    width: 32vw;
    opacity: 0;
    margin-left: 12vw;
    margin-top: 7vw;
}

.SlideInBoxes.animate-show .left{
    -webkit-animation: section1-left-box-show 1s forwards;
            animation: section1-left-box-show 1s forwards;
}

.SlideInBoxes .box.right{
    float: right;
    width: 45vw;
    opacity: 0;
    margin-right: 15vw;
    margin-top: 7vw;
    padding-left: 3vw;
}

.SlideInBoxes.animate-show .right{
    -webkit-animation: section1-right-box-show 0.5s forwards;
            animation: section1-right-box-show 0.5s forwards;
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;
}

.SlideInBoxes.animate-hide .box
{
    -webkit-animation: section1-box-hide 0.5s forwards;
            animation: section1-box-hide 0.5s forwards;
}


.ScrollMarquee .marqueeText{
    font-family: 'Work Sans';
    font-size: 20vw;
    font-weight: bold;
    line-height: 0.8em;
    color: #1a0000;
}

